body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 22px;
}

.wrapper {
  margin-top: 150px;
  text-align: center;
  border-bottom: 2px solid black;
  padding: 30px;
}
.article-heading {
  text-align: center;
  font-size: 80px;
}
.blog-text {
  display: block;
  text-align: justify;
  margin: 10px 150px;
}

nav {
  border-bottom: 4px solid black;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: black;
}

.link {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
  text-decoration: none;
  color: white;
  font-weight: bold;
  padding: 16px 32px;
}

.link:hover {
  background: black;
  color: white;
}
.link-log {
  position: absolute;
  top: 10px;
  right: 20px;
  color: black;
  background-color: white;
  text-decoration: none;
  padding: 10px;
  border-radius: 7px;
}

.article-list-item {
  display: block;
  margin: auto;
  text-align: center;
  border-radius: 5px;
  text-decoration: none;
  width: 600px;
  height: 50px;
  color: white;
  background-color: black;
}
.article-list-item:hover {
  color: black;
  background-color: white;
}
.upvote {
  color: white;
  background-color: black;
  font-size: 20px;
  margin-right: 10px;
  padding: 10px;
  border-radius: 40px;
}
#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input,
textarea {
  display: block;
  margin: auto;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 320px;
  height: 50px;
  color: white;
  background-color: black;
}

#upvotes-section button,
p {
  display: inline-block;
  margin-right: 16px;
}
.comment{
  border-bottom: 1px solid gray;
  margin: 20px 20px;
 
}
.comment h4{
  margin-bottom: 4px;
}

.comments{
  text-align: left;
  margin: 10px 150px;
}